import { useCallback } from 'react';
import { atom, useAtom } from 'jotai';

import type { CookHistory, Recipe, RewardCuisine, RewardIngredient } from 'libs/schema';
import { mockCookHistory, mockRewardCuisines, mockRewardIngredients } from 'libs/mockData';
import { PartialRequired } from 'libs/utils';

interface UserState {
  posts: CookHistory[];
  rewards: {
    cuisine: Record<RewardCuisine, number>;
    ingredient: Record<RewardIngredient, number>;
  };
}

const initialState: UserState = {
  posts: mockCookHistory,
  rewards: {
    cuisine: mockRewardCuisines,
    ingredient: mockRewardIngredients,
  },
};

export const UserAtom = atom(initialState);

export const useUserAtom = () => {
  const [state, setState] = useAtom(UserAtom);

  const addPost = useCallback(
    (newPost: Recipe) => {
      const newPostId = (state.posts.length + 1).toString();
      setState((prev) => ({
        ...prev,
        posts: [
          {
            ...newPost,
            id: newPostId,
            comment: '',
            img: '',
            isDone: false,
          },
          ...prev.posts,
        ],
      }));
      return newPostId;
    },
    [setState, state.posts.length],
  );

  const editPost = useCallback(
    (newPost: PartialRequired<Partial<CookHistory>, 'id'>) => {
      setState((prev) => ({
        ...prev,
        posts: prev.posts.map((p) => (p.id === newPost.id ? { ...p, ...newPost } : p)),
      }));
    },
    [setState],
  );

  const adjustIngredientReward = useCallback(
    (addedReward: Partial<Record<RewardIngredient, number>>) => {
      setState((prev) => {
        const newIngredientReward = { ...prev.rewards.ingredient };
        (Object.entries(addedReward) as [RewardIngredient, number][]).forEach(
          ([ingredient, addedCtn]) => {
            newIngredientReward[ingredient] += addedCtn;
          },
        );
        return { ...prev, rewards: { ...prev.rewards, ingredient: newIngredientReward } };
      });
    },
    [setState],
  );

  const adjustCuisineReward = useCallback(
    (addedReward: Partial<Record<RewardCuisine, number>>) => {
      setState((prev) => {
        const newCuisineReward = { ...prev.rewards.cuisine };
        (Object.entries(addedReward) as [RewardCuisine, number][]).forEach(
          ([cuisine, addedCtn]) => {
            newCuisineReward[cuisine] += addedCtn;
          },
        );
        return { ...prev, rewards: { ...prev.rewards, cuisine: newCuisineReward } };
      });
    },
    [setState],
  );

  return {
    userState: state,
    addPost,
    editPost,
    adjustIngredientReward,
    adjustCuisineReward,
  };
};
