import { useCallback, useEffect, useState, type FunctionComponent } from 'react';
import { useNavigate } from 'react-router-dom';

import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Skeleton from '@mui/material/Skeleton';

import { Page, Recipe } from 'libs/schema';
import { pageSettings } from 'libs/settings';
import { useScanResultAtom } from 'stores/atoms/scanResult';
import { useSnackbarAtom } from 'stores/atoms/snackbar';
import { useUserAtom } from 'stores/atoms/user';
import { fileToBase64, getRandomRewards } from 'libs/utils';

import MainLayout from 'components/common/MainLayout';
import RecipeCard from 'components/RecipeCard';

const Scan: FunctionComponent = () => {
  const navigate = useNavigate();

  const { scanResult, addScanResult } = useScanResultAtom();
  const { showSnackbar } = useSnackbarAtom();
  const { addPost, adjustIngredientReward } = useUserAtom();

  const [isGetReward, setIsGetReward] = useState(false);
  const [isGetRecommend, setIsGetRecommend] = useState(false);
  const [uploadedPhotoUrl, setUploadedPhotoUrl] = useState<string | null>(null);

  useEffect(() => {
    if (isGetReward) return;
    setIsGetReward(true);

    const newRewards = getRandomRewards(1);
    adjustIngredientReward(newRewards);
    showSnackbar({
      severity: 'success',
      message: 'You got 1 ingredient, check it at REWARDS.',
    });
  }, [isGetReward, adjustIngredientReward, showSnackbar]);

  useEffect(() => {
    void (async () => {
      if (scanResult.uploadedPhoto) {
        const base64Photo = await fileToBase64(scanResult.uploadedPhoto);
        setUploadedPhotoUrl(base64Photo);
      }
    })();
  }, [scanResult.uploadedPhoto]);

  useEffect(() => {
    if (isGetRecommend) return;
    setIsGetRecommend(true);

    window.setTimeout(() => {
      addScanResult();
    }, 500);
  }, [isGetRecommend, addScanResult]);

  const handleMakeCuisine = useCallback(
    (recipe: Recipe) => {
      const newPostId = addPost(recipe);
      navigate(`${pageSettings[Page.COOKING].route}?cuisine=${newPostId}`);
    },
    [navigate, addPost],
  );

  return (
    <MainLayout>
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 0.75 }}>
        <Typography variant="h5" sx={{ mt: 0.75 }}>
          Your Photo
        </Typography>
        {uploadedPhotoUrl && (
          <Box
            sx={(theme) => ({
              background: theme.palette.background.default,
              p: 1.5,
              borderRadius: 1,
            })}
          >
            <img src={uploadedPhotoUrl} alt="Captured" style={{ width: '100%' }} />
          </Box>
        )}
        <Typography variant="h5" sx={{ mt: 0.75 }}>
          Suggestions
        </Typography>
        {scanResult.recommendedRecipes ? (
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1.5 }}>
            {scanResult.recommendedRecipes?.map((recipe, idx) => (
              // eslint-disable-next-line react/no-array-index-key
              <RecipeCard key={idx} {...recipe} type="collapse-detail">
                <Button
                  variant="contained"
                  onClick={() => {
                    handleMakeCuisine(recipe);
                  }}
                  sx={{ width: '100%' }}
                >
                  Make This Cuisine
                </Button>
              </RecipeCard>
            ))}
          </Box>
        ) : (
          Array.from({ length: 3 }, (_, idx) => (
            <Skeleton
              // eslint-disable-next-line react/no-array-index-key
              key={idx}
              variant="rounded"
              width="100%"
              height={122.75}
            />
          ))
        )}
      </Box>
    </MainLayout>
  );
};

export default Scan;
