import {
  type ChangeEvent,
  type CSSProperties,
  useCallback,
  useEffect,
  useState,
  type FunctionComponent,
  useMemo,
} from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';

import Button from '@mui/material/Button';
import Skeleton from '@mui/material/Skeleton';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Paper from '@mui/material/Paper';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import OutdoorGrillIcon from '@mui/icons-material/OutdoorGrill';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import Grid from '@mui/material/Unstable_Grid2';

import { Page, RewardIngredient } from 'libs/schema';
import { ingredientToImage, pageSettings } from 'libs/settings';
import { capitalize, fileToBase64, getRandomRewards, uploadPhotoFile } from 'libs/utils';
import congratulationImg from 'assets/img/congratulation.png';
import { useSnackbarAtom } from 'stores/atoms/snackbar';
import { useUserAtom } from 'stores/atoms/user';

import MainLayout from 'components/common/MainLayout';
import RecipeCard from 'components/RecipeCard';

interface ImageWithSkeletonProps {
  src: string;
  alt: string;
  style: CSSProperties;
  skeleton: {
    width: number | string;
    height: number | string;
  };
}

const ImageWithSkeleton: FunctionComponent<ImageWithSkeletonProps> = ({
  src,
  alt,
  style,
  skeleton: { width, height },
}: ImageWithSkeletonProps) => {
  const [isLoading, setIsLoading] = useState(true);

  return (
    <Box position="relative">
      {isLoading && (
        <Skeleton variant="rectangular" width={width} height={height} animation="wave" />
      )}
      <img src={src} style={style} alt={alt} onLoad={() => setIsLoading(false)} />
    </Box>
  );
};

interface EarnedCardProps {
  ingredient: RewardIngredient;
}

const EarnedCard: FunctionComponent<EarnedCardProps> = ({ ingredient }) => (
  <Paper
    sx={{
      display: 'flex',
      flexDirection: 'column',
      p: 1.5,
      gap: 1.5,
      alignItems: 'center',
    }}
  >
    <img alt={ingredient} src={ingredientToImage[ingredient]} style={{ maxWidth: '100%' }} />
    <Typography variant="h6">{capitalize(ingredient)}</Typography>
  </Paper>
);

interface EarnedRewardProps {
  ingredients: RewardIngredient[];
  onBackToCooking: () => void;
}

const EarnedReward: FunctionComponent<EarnedRewardProps> = ({ ingredients, onBackToCooking }) => {
  const navigate = useNavigate();

  const handleToRewards = useCallback(() => {
    navigate(pageSettings[Page.REWARDS].route);
  }, [navigate]);

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3, alignItems: 'center', mt: 5 }}>
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1.5, alignItems: 'center' }}>
        <img
          alt="congratulation"
          src={congratulationImg}
          style={{
            width: '64px',
          }}
        />
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 0.25, alignItems: 'center' }}>
          <Typography variant="h5">Congratulations</Typography>
          <Typography variant="body2" color="textSecondary">
            You got 3 new ingredient cards!
          </Typography>
        </Box>
      </Box>
      <Grid container spacing={2} mb={1}>
        {ingredients.map((ingredient, idx) => (
          // eslint-disable-next-line react/no-array-index-key
          <Grid key={idx} xs={4}>
            <EarnedCard key={ingredient} ingredient={ingredient} />
          </Grid>
        ))}
      </Grid>
      <Box sx={{ display: 'flex', gap: 1.5, flexDirection: 'column', width: '100%' }}>
        <Button variant="contained" onClick={handleToRewards}>
          Go to My Rewards
        </Button>
        <Button variant="outlined" onClick={onBackToCooking}>
          Check My Cuisine
        </Button>
      </Box>
    </Box>
  );
};

const Cooking: FunctionComponent = () => {
  const navigate = useNavigate();

  const [searchParams] = useSearchParams();
  const cuisineId = searchParams.get('cuisine');

  const { showSnackbar } = useSnackbarAtom();
  const { userState, editPost, adjustIngredientReward } = useUserAtom();

  const [uploadedPhotoUrl, setUploadedPhotoUrl] = useState<string | null>(null);
  const [reviewValue, setReviewValue] = useState<string>('');

  const [rewards, setRewards] = useState<Partial<Record<RewardIngredient, number>> | null>(null);

  const initPost = useMemo(
    () => userState.posts.find((p) => p.id === cuisineId),
    [cuisineId, userState.posts],
  );

  useEffect(() => {
    setUploadedPhotoUrl(initPost?.img || '');
    setReviewValue(initPost?.comment || '');
  }, [initPost]);

  const handleBackHome = useCallback(() => {
    navigate(Page.HOME);
  }, [navigate]);

  const handleComplete = useCallback(() => {
    if (!cuisineId) return;
    editPost({
      id: cuisineId,
      img: uploadedPhotoUrl || '',
      comment: reviewValue,
      isDone: true,
    });
    const newRewards = getRandomRewards(3);
    setRewards(newRewards);
    adjustIngredientReward(newRewards);
  }, [cuisineId, editPost, reviewValue, uploadedPhotoUrl, adjustIngredientReward]);

  const handleSave = useCallback(() => {
    if (!cuisineId) return;
    editPost({
      id: cuisineId,
      img: uploadedPhotoUrl || '',
      comment: reviewValue,
    });
    showSnackbar({ message: 'Save successfully!', severity: 'success' });
  }, [showSnackbar, editPost, cuisineId, uploadedPhotoUrl, reviewValue]);

  const handleUploadPhoto = useCallback(() => {
    void (async () => {
      try {
        const newPhoto = await uploadPhotoFile();
        const newPhotoUrl = await fileToBase64(newPhoto);
        setUploadedPhotoUrl(newPhotoUrl);
      } catch (error) {
        console.error(error);
      }
    })();
  }, []);

  const handleChangeReviewValue = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    setReviewValue(e.target.value);
  }, []);

  const handleBackToCooking = useCallback(() => {
    setRewards(null);
  }, []);

  const rewardList = useMemo(() => {
    if (!rewards) return null;
    const newList: RewardIngredient[] = [];
    (Object.entries(rewards) as [RewardIngredient, number][]).forEach(([ingredient, ctn]) => {
      for (let i = 0; i < ctn; i += 1) {
        newList.push(ingredient);
      }
    });
    return newList;
  }, [rewards]);

  return (
    <MainLayout>
      {rewards && rewardList ? (
        <EarnedReward ingredients={rewardList} onBackToCooking={handleBackToCooking} />
      ) : null}
      {!rewards && initPost ? (
        <RecipeCard
          {...initPost}
          type="expand-detail"
          childrenBefore={
            initPost.isDone &&
            (initPost.img ? (
              <ImageWithSkeleton
                src={initPost.img}
                alt="Captured"
                style={{ width: '100%' }}
                skeleton={{ width: '100%', height: 150 }}
              />
            ) : (
              <Box
                sx={(theme) => ({
                  height: 140,
                  backgroundColor: theme.palette.grey[300],
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: theme.palette.text.secondary,
                  mb: 1,
                })}
              >
                <OutdoorGrillIcon fontSize="large" />
                <Typography variant="body2" align="center" sx={{ mt: 1 }}>
                  No image
                </Typography>
              </Box>
            ))
          }
        >
          <Typography variant="h6" sx={{ mt: 0.75 }}>
            Your Review
          </Typography>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            <TextField
              value={initPost.isDone && !reviewValue ? '---' : reviewValue}
              onChange={handleChangeReviewValue}
              multiline
              rows={3}
              placeholder={!initPost.isDone ? 'Share your experience...' : undefined}
              sx={(theme) => ({ width: '100%', background: theme.palette.background.default })}
              disabled={initPost.isDone}
            />
            {!initPost.isDone &&
              (uploadedPhotoUrl ? (
                <img
                  src={uploadedPhotoUrl}
                  alt="Uploaded"
                  style={{ maxWidth: '100%', maxHeight: '100%' }}
                />
              ) : (
                <Box
                  sx={(theme) => ({
                    width: '100%',
                    height: 80,
                    border: `2px dashed ${theme.palette.grey[400]}`,
                    borderRadius: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    cursor: 'pointer',
                    color: theme.palette.text.secondary,
                  })}
                  onClick={handleUploadPhoto}
                >
                  <IconButton>
                    <CameraAltIcon fontSize="medium" />
                  </IconButton>
                  <Typography>Upload your photo</Typography>
                </Box>
              ))}
            {initPost.isDone ? (
              <Button variant="contained" onClick={handleBackHome}>
                Back to Home
              </Button>
            ) : (
              <Box sx={{ display: 'flex', gap: 1.5, flexDirection: 'column' }}>
                <Button variant="contained" onClick={handleComplete}>
                  Complete
                </Button>
                <Button variant="outlined" onClick={handleSave}>
                  Save
                </Button>
              </Box>
            )}
          </Box>
        </RecipeCard>
      ) : null}
      {!rewards && !initPost ? (
        <Box sx={{ display: 'flex', gap: 2, flexDirection: 'column', alignItems: 'center' }}>
          <Skeleton variant="rectangular" width="100%" height={200} />
          <Button variant="contained" onClick={handleBackHome}>
            Back to Home
          </Button>
        </Box>
      ) : null}
    </MainLayout>
  );
};

export default Cooking;
