import { useCallback, useEffect, useState, type FunctionComponent } from 'react';
import { useNavigate } from 'react-router-dom';

import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import Skeleton from '@mui/material/Skeleton';
import CardContent from '@mui/material/CardContent';
import CardActionArea from '@mui/material/CardActionArea';
import Typography from '@mui/material/Typography';
import CameraAltIcon from '@mui/icons-material/CameraAlt';

import { useScanResultAtom } from 'stores/atoms/scanResult';
import { pageSettings } from 'libs/settings';
import { type CookHistory, Page, type Recipe } from 'libs/schema';
import { useUserAtom } from 'stores/atoms/user';
import { mockRandomRecipe } from 'libs/mockData';
import { uploadPhotoFile } from 'libs/utils';

import MainLayout from 'components/common/MainLayout';
import RecipeCard from 'components/RecipeCard';
import CookHistoryCard from 'components/CookHistoryCard';

const Home: FunctionComponent = () => {
  const navigate = useNavigate();

  const { addScanPhoto } = useScanResultAtom();
  const { userState, addPost } = useUserAtom();

  const [randomRecipesData, setRandomRecipesData] = useState<Recipe[] | null>();
  const [postsData, setPostsData] = useState<CookHistory[] | null>();

  useEffect(() => {
    if (postsData) return;
    window.setTimeout(() => {
      setPostsData(userState.posts);
    }, 300);
  }, [postsData, userState.posts]);

  useEffect(() => {
    if (randomRecipesData) return;
    window.setTimeout(() => {
      setRandomRecipesData(mockRandomRecipe);
    }, 500);
  }, [randomRecipesData]);

  const handleClickScan = useCallback(() => {
    void (async () => {
      try {
        const newPhoto = await uploadPhotoFile();
        addScanPhoto({ uploadedPhoto: newPhoto });
        navigate(pageSettings[Page.SCAN].route);
      } catch (error) {
        console.error(error);
      }
    })();
  }, [navigate, addScanPhoto]);

  const handleToNewCook = (recipe: Recipe) => {
    const newPostId = addPost(recipe);
    navigate(`${pageSettings[Page.COOKING].route}?cuisine=${newPostId}`);
  };

  const handleToHistoryCook = (cuisineId: string) => {
    navigate(`${pageSettings[Page.COOKING].route}?cuisine=${cuisineId}`);
  };

  return (
    <MainLayout>
      <Card
        sx={(theme) => ({
          background: theme.palette.primary.main,
          color: theme.palette.primary.contrastText,
          flexGrow: 1,
          display: 'flex',
          justifyContent: 'center',
        })}
      >
        <CardActionArea onClick={handleClickScan}>
          <CardContent
            sx={(theme) => ({
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              color: theme.palette.primary.contrastText,
              gap: 0.5,
            })}
          >
            <CameraAltIcon />
            <Typography
              variant="body2"
              sx={(theme) => ({
                color: theme.palette.primary.contrastText,
              })}
            >
              Tap to scan ingredient
            </Typography>
          </CardContent>
        </CardActionArea>
      </Card>
      <Typography variant="h5" sx={{ mt: 0.75 }}>{`Today's Suggestions`}</Typography>
      <Box
        sx={{ display: 'flex', gap: 1, flexShrink: 0, overflowX: 'auto', scrollbarWidth: 'none' }}
      >
        {randomRecipesData
          ? randomRecipesData.map((recipe, idx) => (
              <RecipeCard
                // eslint-disable-next-line react/no-array-index-key
                key={idx}
                type="simple"
                {...recipe}
                sx={{ flexShrink: 0, width: 250 }}
                onClick={() => {
                  handleToNewCook(recipe);
                }}
              />
            ))
          : Array.from({ length: 4 }, (_, idx) => (
              <Skeleton
                // eslint-disable-next-line react/no-array-index-key
                key={idx}
                variant="rounded"
                width={250}
                height={122.75}
                sx={{ flexShrink: 0 }}
              />
            ))}
      </Box>
      <Typography variant="h5" sx={{ mt: 0.75 }}>
        Your Cuisines
      </Typography>
      <Box
        sx={{ display: 'flex', gap: 1, flexShrink: 0, overflowX: 'auto', scrollbarWidth: 'none' }}
      >
        {postsData
          ? postsData.map((p) => (
              <CookHistoryCard
                key={p.id}
                {...p}
                sx={{ flexShrink: 0, width: 275 }}
                onClick={() => {
                  handleToHistoryCook(p.id);
                }}
              />
            ))
          : Array.from({ length: 4 }, (_, idx) => (
              <Skeleton
                // eslint-disable-next-line react/no-array-index-key
                key={idx}
                variant="rounded"
                width={275}
                height={200}
                sx={{ flexShrink: 0 }}
              />
            ))}
      </Box>
    </MainLayout>
  );
};

export default Home;
