import { useCallback } from 'react';
import { atom, useAtom } from 'jotai';

import type { Recipe } from 'libs/schema';
import { mockRecommendRecipe } from 'libs/mockData';

interface ScanResultState {
  uploadedPhoto: File | null;
  recommendedRecipesId: number | null;
  recommendedRecipes: Recipe[] | null;
}

const initialState: ScanResultState = {
  uploadedPhoto: null,
  recommendedRecipesId: null,
  recommendedRecipes: null,
};

export const ScanResultAtom = atom(initialState);

export const useScanResultAtom = () => {
  const [state, setState] = useAtom(ScanResultAtom);

  const addScanPhoto = useCallback(
    ({ uploadedPhoto }: Pick<ScanResultState, 'uploadedPhoto'>) => {
      setState({ uploadedPhoto, recommendedRecipesId: null, recommendedRecipes: null });
    },
    [setState],
  );

  const addScanResult = useCallback(() => {
    setState((prev) => {
      const newId = prev.recommendedRecipesId ? 0 : 1;
      return {
        ...prev,
        recommendedRecipesId: newId,
        recommendedRecipes: mockRecommendRecipe?.[newId],
      };
    });
  }, [setState]);

  const clearScanResult = useCallback(() => {
    setState({ uploadedPhoto: null, recommendedRecipesId: null, recommendedRecipes: null });
  }, [setState]);

  return {
    scanResult: state,
    addScanPhoto,
    addScanResult,
    clearScanResult,
  };
};
